/*------------------------------------------------------------------
@section 1. Base
*/
blockquote,
dl,
dt,
dd,
figure,
hr,
h1,
h2,
h3,
h4,
h5,
h6 iframe,
p,
pre,
ol,
ul,
li,
textarea {
  margin: 0;
}

button,
input,
select {
  margin: 0;
}

img,
svg,
video,
canvas,
audio,
iframe,
embed,
object {
  display: block;
  vertical-align: middle;
}

html {
  font-size: 62.5%;
}

html, body {
  max-width: 100%;
}

body {
  padding: 0;
  margin: 0;
  background: #ffffff;
  color: #45567C;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, sans-serif;
  font-weight: normal;
  font-style: normal;
  font-size: 1.6rem;
  line-height: 1.8;
  overflow-x: hidden !important;
  overflow-y: scroll;
  -webkit-text-size-adjust: none;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

body.boxed {
  background-color: #F2F3F6;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin-top: 3rem;
  margin-bottom: 2rem;
  font-family: inherit;
  font-weight: 600;
  line-height: 1.2;
  color: #45567C;
}

* h1:first-child,
* h2:first-child,
* h3:first-child,
* h4:first-child,
* h5:first-child,
* h6:first-child {
  margin-top: 0;
}

*,
*:before,
*:after {
  border: 0 solid #E1E4EA;
  box-sizing: border-box;
}

a {
  color: #0096ff;
  text-decoration: none;
  transition: background-color 300ms ease-in-out 0ms , border 300ms ease-in-out 0ms , box-shadow 300ms ease-in-out 0ms , color 300ms ease-in-out 0ms , height 300ms ease-in-out 0ms , max-height 300ms ease-in-out 0ms , width 300ms ease-in-out 0ms , line-height 300ms ease-in-out 0ms , margin 300ms ease-in-out 0ms , opacity 300ms ease-in-out 0ms , padding 300ms ease-in-out 0ms , rotate 300ms ease-in-out 0ms , scale 300ms ease-in-out 0ms , transform 300ms ease-in-out 0ms , translate 300ms ease-in-out 0ms , visibility 300ms ease-in-out 0ms;
}

a:hover {
  color: #45567C;
  text-decoration: none;
}

a > * {
  pointer-events: none;
}

p {
  margin-top: 0;
  margin-bottom: 2rem;
}

hr {
  margin-top: 3rem;
  margin-bottom: 2rem;
  border: 0;
  border-top: 1px solid rgba(28, 34, 50, 0.1);
}

.wrapper {
  width: 100%;
  margin-right: auto;
  margin-left: auto;
  -webkit-backface-visibility: hidden;
}

.wrapper .wrapper-inner {
  max-width: 100%;
  margin-right: auto;
  margin-left: auto;
}

.section-block {
  width: 100%;
  padding: 11rem 0 11rem 0;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  background-size: cover;
  background-position: center;
  transition: background-color 300ms ease-in-out 0ms , border 300ms ease-in-out 0ms , box-shadow 300ms ease-in-out 0ms , color 300ms ease-in-out 0ms , opacity 300ms ease-in-out 0ms;
  position: relative;
}

.media-overlay {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 0;
}

.media-overlay + * {
  position: relative;
}

.freeze-wrapper,
.freeze {
  width: 100%;
}

