.row {
  max-width: 114rem;
}

.row {
  margin-right: auto;
  margin-left: auto;
  margin-top: 0;
  margin-bottom: 0;
  flex-basis: 100%;
}

.row.fluid {
  max-width: 100%;
}

.row .row {
  width: auto;
  margin-right: -1.5rem;
  margin-left: -1.5rem;
}

.row.collapse > .col {
  padding-left: 0;
  padding-right: 0;
}

.row.fluid.collapse .row,
.row .row.fluid.collapse {
  margin: auto;
}

.row .section-block .row {
  margin-left: 0;
  margin-right: 0;
}

.col {
  width: 100%;
  padding-left: 1.5rem;
