.input-group,
.input-group .input-icon,
.input-group .form-element {
  margin: 0;
  display: flex;
}

.input-group {
  margin: 0 0 1.5rem 0;
}

.input-group label {
  align-self: center;
}

.input-group .input-icon {
  padding-right: 1.25rem !important;
  padding-left: 1.25rem !important;
  align-items: center;
}

.input-group .input-icon [class*="icon-"] {
  margin: 0;
  font-size: 140%;
}

.input-group .input-icon {
  background: #F2F3F6;
  color: #1C2232;
  border-color: #E1E4EA;
