}

.form-select:after {
  font-family: "Material Icons";
  font-size: 1.9rem;
  color: #7D89A3;
  content: "expand_more";
  pointer-events: none;
  position: absolute;
  right: 1.25rem;
  z-index: 1;
}

.form-select.size-sm:after {
  right: 0.75rem;
}

.form-select.size-md:after {
  right: 1.25rem;
}

.form-select.size-lg:after {
  right: 1.5rem;
}

.form-select.size-xl:after {
  right: 1.75rem;
}

.checkbox,
.radio {
  margin-bottom: 0 !important;
  width: auto;
  border: none;
  opacity: 0;
  position: absolute;
  z-index: 1;
}

.checkbox:focus + label,
.radio:focus + label {
  outline: none;
}

.checkbox + .checkbox-label:before,
.checkbox + .radio-label:before,
.radio + .checkbox-label:before,
.radio + .radio-label:before {
  width: 2rem;
  height: 2rem;
  line-height: 2rem;
  margin-right: 1rem;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  background: #ffffff;
  border: 1px solid #E1E4EA;
  content: '.';
  font-family: "Material Icons";
  text-align: center;
  text-indent: -9999px;
  vertical-align: middle;
}

.checkbox.size-sm + label,
.radio.size-sm + label {
  font-size: 1.1rem;
}

.checkbox.size-sm + label:before,
.radio.size-sm + label:before {
  width: 1.6rem;
  height: 1.6rem;
  font-size: 90%;
  line-height: 1.6rem;
}

.checkbox.size-md + label:before,
.radio.size-md + label:before {
  width: 2rem;
  height: 2rem;
  font-size: 100%;
  line-height: 2rem;
}

.checkbox.size-lg + label,
.radio.size-lg + label {
  font-size: 1.4rem;
}

.checkbox.size-lg + label:before,
.radio.size-lg + label:before {
  width: 2.4rem;
  height: 2.4rem;
  font-size: 130%;
  line-height: 2.4rem;
}

.checkbox.size-xl + label,
.radio.size-xl + label {
  font-size: 1.5rem;
}

.checkbox.size-xl + label:before,
.radio.size-xl + label:before {
  width: 2.7rem;
  height: 2.7rem;
  font-size: 150%;
  line-height: 2.7rem;
}

.checkbox + label:before {
  border-radius: 0.4rem;
}

.checkbox:checked + label:not([class*="color-"]) {
  opacity: 1;
  color: #1C2232;
}

.checkbox:checked + label:before {
  content: "check_box";
  background: #ffffff;
  color: #0096ff;
  text-indent: 0;
}

.radio + label:before {
  border-radius: 50%;
}

.radio:checked + label:not([class*="color-"]) {
  opacity: 1;
  color: #1C2232;
}

.radio:checked + label:before {
  background: #0096ff;
  box-shadow: inset 0px 0px 0px 4px #ffffff;
}

.checkbox-label,
.radio-label {
  margin-bottom: 1.5rem;
  cursor: pointer;
}

.checkbox,
.radio {
  color: #9EA7BB;
}

.checkbox + label,
.radio + label {
  margin-right: 1rem;
}

.center .checkbox + label,
.center .radio + label {
  margin-right: 0.5rem;
  margin-left: 0.5rem;
}

.right .checkbox + label,
.right .radio + label {
  margin-right: 0;
  margin-left: 1rem;
}

.form-element:focus,
.form-element select:focus {
  background: #ffffff;
  color: #1C2232;
  border: 1px solid #0096ff;
  box-shadow: none;
  outline: none;
  z-index: 1;
}

.form-element:focus + label:before {
  border: 1px solid #0096ff;
}

.form-element.required-field,
.form-element.required-field + label:before {
  background-color: #ffffff;
  border-color: #f14844;
  box-shadow: none;
  color: #f14844;
  position: relative;
}

.checkbox.required-field {
  position: absolute;
}

