/*------------------------------------------------------------------
@section 14. Icon
*/
[class*="icon-"] {
  margin: 0 1rem 1rem 0;
  font-size: 2.4rem;
  text-decoration: none;
}

[class*="icon-"].size-sm {
  font-size: 1.6rem;
}

[class*="icon-"].size-md {
  font-size: 2.4rem;
}

[class*="icon-"].size-lg {
  font-size: 3.2rem;
}

[class*="icon-"].size-xl {
  font-size: 3.8rem;
}

.center [class*="icon-"] {
  margin-right: 0.5rem;
  margin-left: 0.5rem;
}

.right [class*="icon-"] {
  margin-right: 0;
  margin-left: 1rem;
}

/*------------------------------------------------------------------
@section 15. Image
*/
img {
  max-width: 100%;
  height: auto;
  display: inline-block;
  vertical-align: middle;
  border: none;
  border-radius: inherit;
  outline: none;
}

img.float-left,
.thumbnail.float-left {
  margin: 0 2rem 1rem 0;
}

img.float-right,
.thumbnail.float-right {
  margin: 0 0 1rem 2rem;
}

img .image-mask,
.thumbnail .image-mask {
  height: 100%;
  display: block;
  overflow: hidden;
}

.thumbnail {
  margin-bottom: 2rem;
  position: relative;
  overflow: hidden;
  display: inline-block;
  vertical-align: bottom;
}

.thumbnail .bg-image {
  width: 100%;
  height: 100%;
  display: block;
  transition-property: opacity, transform;
}

.thumbnail video {
  max-width: 100%;
  height: auto;
  position: relative;
  z-index: 1;
}

.thumbnail video + img {
  position: absolute;
  top: 0;
  z-index: 0;
}

.thumbnail-video {
  line-height: 0;
}

.thumbnail .caption-below {
  width: 100%;
  padding: 1rem 1.5rem;
  font-size: 1.2rem;
  font-style: italic;
  border: 1px solid #E1E4EA;
  float: left;
}

.content-over {
  width: 100%;
  height: 100%;
  padding: 3rem;
  display: flex;
  position: absolute;
  bottom: 0;
  z-index: 2;
}

.content-over > span,
.content-over > div {
  width: 100%;
}

.thumbnail > a {
  width: 100%;
  height: 100%;
  display: block;
  outline: none;
  border: none;
  overflow: hidden;
  position: relative;
  top: 0;
  left: 0;
}

.thumbnail .rollover-content {
  width: 100%;
  height: 100%;
  padding: 3rem;
  margin: 0;
  display: flex;
  border: none;
  border-radius: inherit;
  background: rgba(0, 0, 0, 0.5);
  font-size: 1.2rem;
  opacity: 0;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
  -webkit-transform: translateZ(0);
  transform: translateZ(0);
  -webkit-backface-visibility: hidden;
}

.thumbnail .rollover-content > span,
.thumbnail .rollover-content > div {
  width: 100%;
}

.thumbnail:hover .rollover-content {
  opacity: 1;
}

.thumbnail:hover .rollover-content,
.thumbnail:hover img,
.thumbnail:hover .bg-image {
  transform: translate3d(0, 0, 0) rotate3d(1, 1, 1, 0deg) scale3d(1, 1, 1);
}

.thumbnail .rollover-content,
.thumbnail a.rollover-content:hover {
  color: #fff;
}

.overlay-fade-out .rollover-content,
.overlay-fade-img-scale-in .rollover-content,
.overlay-fade-img-scale-out .rollover-content {
  opacity: 1;
}

.overlay-fade-out:hover .rollover-content,
.overlay-fade-img-scale-in:hover .rollover-content,
.overlay-fade-img-scale-out:hover .rollover-content {
  opacity: 0;
}

.overlay-slide-in-top .rollover-content {
  opacity: 0;
  transform: translate3d(0, -100%, 0);
}

.overlay-slide-in-right .rollover-content {
  opacity: 0;
  transform: translate3d(100%, 0, 0);
}

.overlay-slide-in-bottom .rollover-content {
  opacity: 0;
  transform: translate3d(0, 100%, 0);
}

.overlay-slide-in-left .rollover-content {
  opacity: 0;
  transform: translate3d(-100%, 0, 0);
}

.overlay-scale-in .rollover-content {
  opacity: 0;
  transform: scale3d(0.1, 0.1, 1);
}

.overlay-rotate-in .rollover-content {
  opacity: 0;
  transform: rotate3d(0.1, 0.1, 1, -180deg);
}

.overlay-img-slide-right:hover img,
.overlay-img-slide-right:hover .bg-image {
  transform: translate3d(100%, 0, 0);
}

.overlay-img-slide-right .rollover-content {
  opacity: 1;
  transform: translate3d(-100%, 0, 0);
}

.overlay-img-slide-left:hover img,
.overlay-img-slide-left:hover .bg-image {
  transform: translate3d(-100%, 0, 0);
}

.overlay-img-slide-left .rollover-content {
  opacity: 1;
  transform: translate3d(100%, 0, 0);
}

.overlay-img-slide-down:hover img,
.overlay-img-slide-down:hover .bg-image {
  transform: translate3d(0, 100%, 0);
}

.overlay-img-slide-down .rollover-content {
  opacity: 1;
  transform: translate3d(0, -100%, 0);
}

.overlay-img-slide-up:hover img,
.overlay-img-slide-up:hover .bg-image {
  transform: translate3d(0, -100%, 0);
}

.overlay-img-slide-up .rollover-content {
  opacity: 1;
  transform: translate3d(0, 100%, 0);
}

.overlay-img-passpartout:hover img,
.overlay-img-passpartout:hover .img {
  transform: scale3d(2, 2, 1);
}

.overlay-img-passpartout:hover .image-mask {
  transform: scale3d(0.7, 0.7, 1);
}

.img-scale-in:hover img,
.img-scale-in:hover .bg-image,
.img-scale-out img,
.img-scale-out .bg-image,
.overlay-fade-img-scale-out img,
.overlay-fade-img-scale-out .bg-image,
.overlay-fade-img-scale-in:hover img,
.overlay-fade-img-scale-in:hover .bg-image,
.overlay-img-scale-in:hover img,
.overlay-img-scale-in:hover .bg-image {
  opacity: 1;
  transform: scale3d(1.2, 1.2, 1);
}

.overlay-img-scale-in .rollover-content {
  transform: scale3d(0.4, 0.4, 1);
}

/*------------------------------------------------------------------
@section 16. List
*/
ul, ol {
  margin: 0 0 2rem 2rem;
  padding: 0;
  font-size: inherit;
}

dl {
  margin: 0 0 2rem 0;
}

dt {
  font-weight: bold;
}

dd {
  padding-left: 3rem;
  margin: 0 0 2rem 0;
}

ul {
  list-style: disc outside;
}

ol {
  list-style: decimal outside;
}

ul ul, ul ol,
ol ol, ol ul {
  margin: 0.4rem 0 0.4rem 3rem;
}

.list-none {
  margin-left: 0;
  list-style: none;
}

.list-horizontal {
  margin-left: 0;
}

.list-horizontal > li {
  margin-right: 1rem;
  display: inline-block;
}

.list-horizontal.left > li {
  margin-right: 1rem;
}

.list-horizontal.right > li {
  margin-left: 1rem;
  margin-right: 0;
}

.list-horizontal.center > li {
  margin-left: 0.5rem;
  margin-right: 0.5rem;
}

.list-horizontal:not(.center) > li:last-child {
  margin-right: 0;
}

.list-horizontal.right > li:last-child {
  margin-left: 0;
}

ul li [class*="icon-"] {
  margin-bottom: 0;
}

.list-group {
  margin: 0 0 2rem 0;
  list-style: none;
  overflow: hidden;
}

.list-group:after {
  height: 1px;
  content: ".";
  display: block;
  overflow: hidden;
}

.list-group.flush * {
  border-right: 0 !important;
  border-left: 0 !important;
}

.list-group.flush :first-child > * {
  border-top: none;
}

.list-group.flush :last-child > * {
  border-bottom: none;
}

.list-group-item {
  margin: 0 0 -1px 0;
  display: block;
  background: none;
  border: 1px solid #E1E4EA;
  color: #45567C;
}

.list-group-item:hover {
  background: #F2F3F6;
  border-color: #E1E4EA;
  color: #1C2232;
}

.list-group-item.active {
  background-color: #0096ff;
  border-color: #0096ff;
  color: #ffffff;
}

/*------------------------------------------------------------------
@section 17. Media
*/
.video-container {
  margin-bottom: 2rem;
  overflow: hidden;
  position: relative;
}

.video-container > * {
  position: absolute;
}

.bg-video-container {
  width: 100%;
  height: 100%;
  position: absolute;
}

.bg-video-container > * {
  position: inherit;
}

iframe {
  border: none;
  overflow: hidden;
}

/*------------------------------------------------------------------
@section 19. Navigation
*/
.nav-bar {
  width: 100%;
}

.nav-bar-inner {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
}

.navigation ul {
  padding: 0;
  margin: 0;
  list-style: none;
}

.navigation ul li {
  position: relative;
}

.navigation ul li > a {
  display: block;
  text-decoration: none;
}

.navigation ul li > a > span {
  position: relative;
}

.navigation ul li > a [class*="icon-"] {
  margin-bottom: 0;
}

.navigation ul > li > a {
  height: 100%;
  display: flex;
  align-items: center;
}

.navigation ul > li > a > span {
  line-height: 1.4;
}

.navigation ul > li > a > span .description {
  display: block;
  font-size: 1.2rem;
}

.main-menu-item {
  height: inherit;
  padding: 1rem 1.5rem;
  font-weight: 400;
  line-height: inherit;
  font-size: 1.4rem;
  color: #45567C;
}

.main-menu-item:hover {
  color: #0096ff;
}

.main-menu-item.active {
  background-color: transparent;
  color: #0096ff;
  text-decoration: none;
  font-weight: 400;
}

.main-menu-item.active:hover {
  background-color: transparent;
  color: #0096ff;
}

.navigation ul li:hover > .sub-menu,
.navigation ul li:hover > .mega-sub-menu {
  visibility: visible;
  opacity: 1;
}

.sub-menu,
.dropdown-menu {
  min-width: 20rem;
}

.sub-menu,
.dropdown-menu {
  text-align: left;
  line-height: 1.6;
  background: #ffffff;
  box-shadow: 0 0.2rem 1.8rem rgba(0, 0, 0, 0.15);
  border: none;
  border-radius: 0.3rem;
  opacity: 0;
  visibility: hidden;
  position: absolute;
  z-index: 1000;
}

.sub-menu li,
.dropdown-menu li {
  background: none;
}

.sub-menu li:last-child,
.dropdown-menu li:last-child {
  border-bottom: none;
}

.sub-menu-item {
  padding: 1rem 1.5rem;
  color: #7D89A3;
  font-size: 1.3rem;
  font-weight: 400;
}

.sub-menu-item:hover {
  background-color: transparent;
  color: #0096ff;
}

.sub-menu-item.active {
  background-color: transparent;
  color: #0096ff;
  text-decoration: none;
  font-weight: 400;
}

.dropdown-menu {
  margin: 0;
}

.sub-menu .sub-menu {
  top: 0;
  left: 20rem;
}

.sub-menu-right {
  right: 0;
  left: auto;
}

.sub-menu-right .sub-menu {
  right: 20rem;
  left: auto;
}

.sub.menu-left {
  right: auto;
  left: 0;
}

.sub.menu-left .sub-menu {
  right: auto;
  left: 20rem;
}

.navigation li.contains-mega-sub-menu {
  position: inherit;
}

.mega-sub-menu {
  height: auto;
  padding: 2rem 0;
  margin-left: 0;
  margin-right: 0;
  line-height: 1.6;
  background: #ffffff;
  box-shadow: 0 0.2rem 1.8rem rgba(0, 0, 0, 0.15);
  border: none;
  border-radius: 0.3rem;
  opacity: 0;
  overflow: hidden;
  visibility: hidden;
  display: flex;
  flex-wrap: wrap;
  position: absolute;
