/*------------------------------------------------------------------
@section 71. Preloader
*/
.preloader {
  width: 5rem;
  height: 5rem;
  margin-top: -2.5rem;
  margin-left: -2.5rem;
  position: absolute;
  top: 50%;
  left: 50%;
}

.preloader:before {
  content: '';
  display: block;
  padding-top: 100%;
}

.preloader #circle {
  background: none;
  width: 100%;
  height: 100%;
  margin: auto;
  border-radius: 50%;
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  transform-origin: center center;
  animation: rotate 2s linear infinite;
}

.preloader .stroke {
  stroke-dasharray: 1, 200;
  stroke-dashoffset: 0;
  animation: dash 1.5s ease-in-out infinite, color 6s ease-in-out infinite;
  stroke-linecap: round;
  stroke-width: 3px;
}

/* ie fallback */
.ie-browser:not(.webkit) .preloader #circle {
  border: 3px solid rgba(0, 0, 0, 0);
  border-bottom-color: plugin-preloader-color-1;
  animation: rotate 1.5s ease-in-out infinite, color 6s ease-in-out infinite;
}

/* Animation*/
@keyframes rotate {
  100% {
    transform: rotate(360deg);
  }
}

@keyframes dash {
  0% {
    stroke-dasharray: 1, 200;
    stroke-dashoffset: 0;
  }
  50% {
    stroke-dasharray: 89, 200;
    stroke-dashoffset: -35px;
  }
  100% {
    stroke-dasharray: 89, 200;
    stroke-dashoffset: -124px;
  }
}

@keyframes color {
  100%,
  0% {
    stroke: #07090C;
    border-bottom-color: #07090C;
  }
  40% {
    stroke: #07090C;
    border-bottom-color: #07090C;
  }
  66% {
    stroke: #07090C;
    border-bottom-color: #07090C;
  }
  80%,
  90% {
    stroke: #07090C;
    border-bottom-color: #07090C;
  }
}

